import React from 'react';
import style from '../Form.module.scss'

class FormInput extends React.Component {
  
  render() {
	const {value, name, refCallback, placeholder, type = 'text', required = false, onChange} = this.props;
    return(
	<input type={type} value={value} ref={refCallback} name={name} required={required} placeholder={placeholder} className={style.formControl} onChange={onChange}/>
	)
  }
}
export default FormInput;